<template>
    <div class="login-form" :style="activeTab === 'login' ? 'display: block;' : 'display: none;'">
        <div class="input-group">
            <input type="text" placeholder="이메일" class="input-field" v-model="userEmail" ref="emailInput" @keyup.enter="login">
        </div>
        <div class="input-group">
            <input type="password" placeholder="비밀번호" class="input-field" v-model="userPassword" ref="passwordInput" @keyup.enter="login">
        </div>
        <div class="checkbox-group">
            <label class="checkbox-label">
                <input type="checkbox" class="checkbox">
                아이디 저장
            </label>
        </div>

        <!-- 유효성 검사 결과 output -->
        <div id="output">{{ msg }}</div>

        <!-- 로그인 버튼 -->
        <button @click="login()" class="login-btn">
            <span>로그인</span>
        </button>

        <!-- 하단 링크 -->
        <div class="bottom-links">
            <div class="signup-container">
                <router-link to="/signup" class="signup">회원가입</router-link>
            </div>
            <div class="searchAcc-container">
                <a href="#">비밀번호 찾기</a>
            </div>
        </div>
    </div>
</template>
<script>
import store from '@/scripts/store';
import axios from 'axios';

export default {
    name: 'LoginForm',
    props: {
        activeTab: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            userEmail: '',
            userPassword: '',
            msg: '',
        }
    },
    methods: {
        login() {
            this.msg = '';

            // 유효성 검사
            // 1-1. 이메일 공란
            if (!this.userEmail) {
                this.msg = '이메일을 입력해주세요.';
                this.$nextTick(() => {
                    this.$refs.emailInput.focus();
                });
                return;
            }

            // 1-2. 이메일 형식 검사
            if (!this.validateEmail(this.userEmail)) {
                this.msg = '올바른 이메일 형식을 입력해주세요.';
                return;
            }

            // 1-3. 비밀번호 공란
            if (!this.userPassword) {
                this.msg = '비밀번호를 입력해주세요.';
                this.$nextTick(() => {
                    this.$refs.passwordInput.focus();
                });
                return;
            }

            axios.post('/api/user/login', {
                userEmail: this.userEmail,
                userPassword: this.userPassword
            })
                .then(response => {
                    alert('로그인 성공!');
                    store.commit('setAccount', response.data);
                    console.log('로그인 성공:', response.data);
                    this.$router.push('/');
                })
                .catch(error => {
                    if (error.response && error.response.status === 401) {
                        this.msg = '이메일 또는 비밀번호가 올바르지 않습니다.';
                    } else {
                        this.msg = '로그인 중 오류가 발생했습니다.';
                    }
                })
        },
        validateEmail(email) {
            const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
            return emailRegex.test(email);
        }
    }
}
</script>
<style scoped>
/* 로그인 폼 */
.login-form {
    padding: 30px;
}

.input-group {
    margin-bottom: 15px;
}

.input-field {
    width: 100%;
    padding: 15px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 14px;
    transition: border-color 0.3s ease;
}

.input-field:focus {
    outline: none;
    border-color: #007bff;
}

.input-field::placeholder {
    color: #999;
}

.checkbox-group {
    margin: 20px 0;
}

.checkbox-label {
    display: flex;
    align-items: center;
    font-size: 14px;
    color: #666;
    cursor: pointer;
}

.checkbox {
    margin-right: 8px;
}

/* 유효성 검사 output */
#output {
    color: #e74c3c;
}

/* 로그인 버튼 */
.login-btn {
    width: 100%;
    padding: 15px;
    background: #333;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    margin-top: 20px;
    transition: background-color 0.3s ease;
}

.login-btn:hover {
    background: #555;
}

.btn-highlight {
    background: #007bff;
    color: white;
    padding: 4px 8px;
    border-radius: 4px;
    font-size: 12px;
    margin-left: 10px;
}

/* 하단 링크 */
.bottom-links {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 12px;
    color: #666;
}

.bottom-links a {
    color: #666;
    text-decoration: none;
    transition: color 0.3s ease;
    padding: 5px;
}

.bottom-links a:hover {
    color: #333;
}

.bottom-links .signup {
    color: #666;
    text-decoration: none;
    transition: color 0.3s ease;
    padding: 5px;
}

.bottom-links .signup:hover {
    color: #333;
}

.support-link {
    background: #007bff;
    color: white;
    padding: 4px 8px;
    border-radius: 4px;
    font-size: 12px;
    cursor: pointer;
}

/* 반응형 디자인 */
@media (max-width: 480px) {
    .login-form {
        padding: 20px;
    }
}
</style>