                <div class="tab-menu">
                    <div :class="activeTab === 'login' ? 'tab active' : 'tab'" @click="setActiveTab('login')">로그인</div>
                    <div :class="activeTab === 'social' ? 'tab active' : 'tab'" @click="setActiveTab('social')">소셜 로그인
