<script>
import SocialLogin from '../components/login/SocialLogin.vue'
import LoginForm from '../components/login/LoginForm.vue'

export default {
    name: 'Login',
    components: {
        SocialLogin,
        LoginForm
    },
    data() {
        return {
            activeTab: 'login',
        }
    },
    methods: {
        setActiveTab(tab) {
            this.activeTab = tab;
        },
    },
    mounted() {

    } // ready
}
</script>
